body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.d-card{
  margin: 20px;
}

.d-card-img {
  border-radius: 10px;
  width: 400px;
  height: 480px;
  box-shadow: 0px 0px 3px 1px #ccc;
}

.d-card-info {
  margin-top:10px;
  min-height: 100px;
}

.d-card-title {
  font-size:24px;
  margin: 0px;
}

.d-card-subtitle {
  font-size:16px;
  margin:0px;
}
.success-msg{
  color: #57A773;
  padding:10px 15px;
}

.error-msg{
  color: #EE6532;
  padding:10px 15px;
}

.portrait-bio{
  width:300px;
  font-size:12px;
  text-align: center;
}

.subject-list{
  width: 65%;
    font-size: 24px;
    margin: auto;
    list-style-type: none;
}

.hero-title{
  font-family: 'Stint Ultra Expanded', cursive;
  font-size: 70px;
}

.subtitle{
  font-family: 'Karla', sans-serif;
  font-size: 34px;
}